import { Component, h, Prop } from '@stencil/core';
import { cart } from '../../global/data/cart';

@Component({
  tag: 'ks-cart-payment-calculators',
  styleUrl: 'cart-payment-calculators.css'
})
export class CartShippingMessage {
    @Prop() creditagricoleId: number;
    @Prop() creditagricoleParameters: string;

    render() {
        return (this.creditagricoleParameters && cart.get("activePayment") == this.creditagricoleId) ?
                <ks-product-calculator-ca
                    price={cart.get("totalValue")}
                    parameters={this.creditagricoleParameters}>
                    <ks-button round name={cart.get("strings").installmentsButton}/>
                </ks-product-calculator-ca>
            : null
        ;
    }
}